import {SingleSignOnBanner} from '@github-ui/single-sign-on-banner'

export type SingleSignOnDropdownPartialProps = {
  base_avatar_url: string
  sso_organizations: Array<{[key: string]: string}>
}

export function SingleSignOnDropdownPartial(props: SingleSignOnDropdownPartialProps) {
  const ssoOrgs = props?.sso_organizations ?? []
  if (ssoOrgs.length === 0) {
    return null
  }

  const orgNames = ssoOrgs.map(o => o['login']).filter(n => n !== undefined)

  return (
    <SingleSignOnBanner
      sx={{flexDirection: ['column', 'column', 'row'], alignItems: ['flex-start', 'flex-start', 'center'], py: 2}}
      protectedOrgs={orgNames}
    />
  )
}

try{ SingleSignOnDropdownPartial.displayName ||= 'SingleSignOnDropdownPartial' } catch {}